<template>
  <div id="app">
      <isdat-nav/>
      <main class="isdat-main">
        <router-view/>
          <isdat-popup-menu></isdat-popup-menu>
      </main>
      <isdat-text-size-indicator/>
  </div>
</template>

<script>
    import IsdatNav from "./IsdatNav";
    import {MUTATIONS} from "./store";
    import IsdatPopupMenu from "./IsdatPopupMenu";
    import IsdatTextSizeIndicator from "./IsdatTextSizeIndicator";

    export default {
        components: {IsdatTextSizeIndicator, IsdatPopupMenu, IsdatNav},
        methods: {
            resizer() {
                this.$store.commit(MUTATIONS.SET_WINDOW_WIDTH);
            },
        },
        mounted() {
            this.resizer();
            window.addEventListener('resize', this.resizer);
        }
    }
</script>

