<template>
    <div class="isdat-text-size-indicator">
        <p>text, {{ text_size }}</p>
    </div>
</template>
<script>
    import {MUTATIONS} from "./store";

    export default {
        name: 'isdat-text-size-indicator',
        data() {
          return {
              computing: false,
          };
        },
        methods: {
          resizer() {
              if (this.computing) return;
              setTimeout(() => {
                  this.computing = true;
                  const el = this.$el;
                  const line_height = el.getBoundingClientRect().height;
                  this.$store.commit(MUTATIONS.SET_TEXT_HEIGHT, line_height);
                  setTimeout(() => { this.computing = false; }, 500);
              }, 500);
          }
        },
        computed: {
          text_size() {
            return this.$store.state.text_height;
          },
        },
        mounted() {
            this.resizer();
            window.addEventListener('resize', this.resizer);
        }
    }
</script>
