<template>
    <div class="isdat-popup-menu" :style="style">
        <nav class="isdat-popup-menu-nav">
            <ul class="isdat-popup-menu-links">
                <li class="isdat-popup-menu-link"><router-link :to="'/presentation'">Présentation du&nbsp;programme<em> Design&nbsp;et&nbsp;pauvreté</em></router-link></li>
                <li class="isdat-popup-menu-link"><router-link :to="'/cycle/1:habitat-et-precarite'"><em>Cycle&nbsp;1</em> Habitat&nbsp;et&nbsp;précarité</router-link></li>
                <li class="isdat-popup-menu-link"><router-link :to="'/cycle/2:travailler-pour-nous-a-caylus'"><em>Cycle&nbsp;2</em> Travailler pour&nbsp;nous à&nbsp;Caylus</router-link></li>
                <li class="isdat-popup-menu-link"><router-link :to="'/cycle/3:design-et-supermarches-cooperatifs'"><em>Cycle&nbsp;3</em> Design&nbsp;et&nbsp;supermarchés&nbsp;coopératifs</router-link></li>
            </ul>
        </nav>
