</template>

<script>
    export default {
        name: 'isdat-popup-menu',
        computed: {
            open: {
                get() {
                    return this.$store.state.menu_open;
                },
                set(v) {
                    this.$store.commit(MUTATIONS.SET_MENU_STATE, v);
                },
            },
            style() {
                return !this.open ? {opacity: 0, pointerEvents: 'none'} : {opacity: 1, pointerEvents: 'initial'}
            }
        }
    }
</script>
